import SearchBox from '@/components/SearchBox'
const PAGE_SIZE = 6
export default {
  components: {
    SearchBox
  },
  data () {
    return {
      menuList: [
        '综合',
        '销量',
        '价格'
      ],
      activeIndex: 0,
      query: '',
      goodsList: [],
      // 是否在请求中
      isLastPage: false,
      // 头部默认为固定定位
      isFixed: false
    }
  },
  onLoad (options) {
    this.query = options.query
    this.reload()
  },
  onPullDownRefresh () {
    // 下拉时，设置头部不fixed
    this.isFixed = false
    this.reload()
  },
  onPageScroll () {
    this.isFixed = true
  },
  onReachBottom () {
    // 加载下一页
    if (!this.isLastPage) {
      this.pageNum++
    }
    this.queryGoods()
  },
  methods: {
    changeIndex (index) {
      this.activeIndex = index
    },
    doSearch (keyword) {
      // console.log(keyword)
      this.query = keyword
      this.reload()
    },
    // 去到商品详情
    toItem (goodsId) {
      wx.navigateTo({ url: '/pages/item/main?goodsId=' + goodsId })
    },
    reload () {
      // 重置页码为1
      this.pageNum = 1
      // 重置goodsList
      this.goodsList = []
      this.isLastPage = false
      this.isRequest = false
      this.queryGoods()
    },
    async queryGoods () {
      // 如果请求中，就不能发请求了
      if (this.isRequest || this.isLastPage) {
        return
      }
      // 发请求前，设置isrequest为true
      this.isRequest = true
      let data = await this.$request({
        url: '/api/public/v1/goods/search',
        data: {
          query: this.query,
          pagenum: this.pageNum,
          pagesize: PAGE_SIZE
        }
      })
      // 请求完后,isRequest为false
      this.isRequest = false
      this.goodsList = [...this.goodsList, ...data.goods]
      console.log(data)
      // 在这里可以判断到是否是最后一页
      if (this.goodsList.length === data.total) {
        this.isLastPage = true
      }
    }
  }

}
